.main{
  border: 1.2px solid #c9c9c9 ;
  height: auto;
  width: 70%;
  padding: 10px;
  justify-content: center;
  /* padding-right: 2%; */
}

.time{
  margin-left: 3%;
}

.editIcon {
  width: 17px;
  height: 17px;
  /* margin-left: 50%; */
}
.deleteIcon {
  width: 17px;
  height: 17px;
  margin-left: 10%;
}

.buttonYes{
border: none;
background-color: #669999;
color: #ffffff;
border-radius: 3px ;
padding: 8px 31px;
text-align: center;
text-decoration: none;
display: inline-block;
font-size: 16px; 
}

.buttonNo{
border: none;
background-color: #669999;
color: #ffffff;
margin-left: 10px;
border-radius: 3px ;
padding: 8px 35px;
text-align: center;
text-decoration: none;
display: inline-block;
font-size: 16px; 
}


.ul{
  list-style-type: none;
}

.noScheduledContent {
  margin-top: 5%;
  margin-left: 23%;
}

.dateTimeDiv{
  float: left;
  width: 70%;
}

.iconDiv{
  float: right;
  width: 30%;
  text-align: right;
}


.modal {
  position: fixed; 
  z-index: 1; 
  padding-top: 250px;
  left: 0%;
  top: 0%;
  width: 100%; 
  height: 100%; 
  overflow: auto; 
  background-color: rgb(0,0,0); 
  background-color: rgba(0,0,0,0.25);
  
}


.modalContent {
  position: relative;
  background-color: #fefefe;
  margin: auto;
  padding-bottom:10px;
  border: 1px solid #888;
  border-radius: 5px ;
  width: 24%;
  -webkit-animation-name: animatetop;
  -webkit-animation-duration: 0.4s;
  animation-name: animatetop;
  animation-duration: 0.4s
}


@-webkit-keyframes animatetop {
  from {top:300px; opacity:0 }  
  to {top:0; opacity:1}
}

@keyframes animatetop {
  from {top:-300px; opacity:0}
  to {top:0; opacity:1}
}


.close {
  color: gray;
  text-align: right;
  font-size: 28px;
  margin-right: 4%;
  padding-top: 0px;
}

.close:hover,.close:focus {
text-decoration: none;
cursor: pointer;
}

.modalBody {padding-left: 20px;}

.iconDetails {
  margin-left:2%;
  float:left; 
} 
.headerStyleScheduled{
  margin-left: 45px;
  color: #232323;
  font-size: 14px;
  margin-top: 14px;
}

@media screen and (max-width: 600px) {
  .main{
    width: 115%;}
}    
